.icon {
  margin-top: 5px;
  margin-bottom: 10px;
  width: 84px;
  height: 80%
}

.row-diy {
  /* margin-top: 10px; */
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  background-color: #242628;
  width: 1560px;
}



input {
  border-radius: 7px;
  width: 78%;
  height: 40px;
  background-color: black;
}

#header-icon {
  /* margin-left:10px; */
  margin-left: 20px
}

#header-nav1 {
  margin-left: 20px;
}

#header-nav2 {
  margin-left: 40px;
}

.bangumi {
  height: 360px;
  width: 1244px;
  z-index: 1;
  position: relative;
  margin-bottom: 20px;
  margin: 0 auto;
}

.bangumi-background {
  opacity:0;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  position:absolute;
  /* transition: opacity 2s ease 0s; */
  /* -webkit-transition: opacity 2s ease 0s; */
  transition-property: opacity;
  transition-duration: 1s;
  transition-timing-function: ease;
  transition-delay: 0s;
}

.bangumi-background-hidden{
  display: none;
}
.bangumi-background-show{
  opacity: 1;
}

.bangumishow {
  background-color: rgba(0, 0, 0, 0.5);
  width: 196px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

#avatar {
  border-radius: 50%;
  width: 52px;
  height: 52px;
  margin-right: 15px;
  margin-top: 0px;
}

#search-input-div {
  margin-left: 35px;
  width: 300px;
}


.right-entry-icon {
  color: #E7E9EB;
  width: auto;
}

.right-entry-text {
  color: #E7E9EB;
}

.bangumishow-text {
  color: #fff;
  line-height: 20px;
  opacity: 0.6;
  font-size: 14px;
  text-align: left;
  overflow: hidden;
  user-select: none;
}

.bangumishow-text-animation {
  background: #151f2c;
  animation-timing-function: normal;
  animation-duration: 0.4s;
  animation-iteration-count: 1;
  animation-name: big;
  animation-fill-mode: forwards;
}

@keyframes big {
  from {
    font-size: 14px;
  }

  to {
    font-size: 17px;
     opacity: 1;
  }
}

#submit {
  background-color: hotpink;
  margin: 6px;
}

.side-list {
  margin-top: 70px;
}

.under-list {
  display: flex;
  flex-direction: row;
  width: 1244px;
  margin: 0 auto;
}

.under-list-div {
  margin-top: -80px;
  z-index: 3;
  position: relative;
  background: linear-gradient(0deg, #111319 14.49%, rgba(17, 19, 25, .8) 60.12%, rgba(20, 20, 20, .0001));
}

.under-list-sub {
  margin-right: 60px;
}

.list-title {
  font-size: 20px;
  margin-right: 6px;
}

.list-subtitle {
  font-size: 14px;
}

.under-list-sub-header {
  margin-bottom: 4px;
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.under-list-sub-row {
  display: flex;
  flex-direction: row;
}

.under-list-sub-column {
  display: flex;
  flex-direction: column;
}

.list-item {
  max-width: 160px;
  line-height: 22px;
  height: 22px;
  display: inline-block;
  font-size: 12px;
  margin-top: 6px;
  margin-right: 34px;
  white-space: nowrap;
}

.hot-module-grid {
  display: grid;
  column-gap: 16px;
  grid-row-gap: 24px;
  grid-template-rows: repeat(2, 138px);
  grid-template-columns: repeat(14, 1fr);
  margin: 40px auto;
  width: 1244px;
}

.hot-audio {
  grid-column-start: 1;
  grid-column-end: 7;
  grid-row-start: 1;
  grid-row-end: 3;
  position: relative;
  background-size: cover;
  height: 100%;

}

.hot-item {
  grid-column-start: auto;
  grid-column-end: span 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.hot-title{
  margin-top: 5px;
  font-size: 14px;
  text-overflow: ellipsis;
  white-space:nowrap;
  overflow:hidden
}

.title{
  color:#e1e1e1;
  font-size: 18px;
    text-overflow: ellipsis;
    white-space:nowrap;
    overflow:hidden
}

.score {
  position: absolute;
  font-size: 20px;
  right: 0;
  bottom: 0;
  padding: 0 8px 2px 0;
  color: #e1e1e1;
  text-align: right;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, .5));
  width: 100%;
}

.rank{
  position: absolute;
  top:0;
  left:0;
}

._img{
  width:100%;
  height:100%;
  border-radius: 8px;
}

.hot-img{
  width:124px;
  height: 100px;
  border-radius: 8px;
}

.season-cover{
  height: 220px;
  position:relative;
}

.season-item{
  margin-right:16px;
  width:164px
}

.hot-list-div
{
  width: 1244px;
  margin:0 auto;
}

.justify-center{
  justify-content: center;
  margin-top: 15px;
}


.main-title{
  font-size: 28px;
}


.text-div{
  flex:1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left:12px;
}
